
export class Website {
  _id: String;
  name: String;
  developerId: String;
  description: String;

  constructor(id: String, name: String, developerId: String, description: String) {
    if ( id !== null) {
      this._id = id;
    }
    this.name = name;
    this.developerId = developerId;
    this.description = description;
  }

}



